export const dynamic = 'force-dynamic';

import connectToDatabase from '@/lib/db';
import Message from '@/models/Message';

async function getMessages() {
    await connectToDatabase();
    const messages = await Message.find({}).sort({ createdAt: -1 });
    return messages;
}

export default async function MessagesPage() {
    const messages = await getMessages();

    return (
        <div>
            <h1 className="text-3xl font-display text-primary mb-8">Messages</h1>

            <div className="bg-surface border border-border rounded-lg overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-surfaceHighlight">
                        <tr>
                            <th className="p-4 text-sm font-medium text-secondary">Date</th>
                            <th className="p-4 text-sm font-medium text-secondary">From</th>
                            <th className="p-4 text-sm font-medium text-secondary">Message</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-border">
                        {messages.map((msg: any) => (
                            <tr key={msg._id} className="hover:bg-surfaceHighlight/50 transition-colors">
                                <td className="p-4 text-secondary whitespace-nowrap">
                                    {new Date(msg.createdAt).toLocaleDateString()} <br />
                                    <span className="text-xs text-secondary/50">{new Date(msg.createdAt).toLocaleTimeString()}</span>
                                </td>
                                <td className="p-4">
                                    <div className="text-primary font-bold">{msg.name}</div>
                                    <div className="text-sm text-secondary">{msg.email}</div>
                                </td>
                                <td className="p-4 text-secondary max-w-md break-words">
                                    {msg.message}
                                </td>
                            </tr>
                        ))}
                        {messages.length === 0 && (
                            <tr>
                                <td colSpan={3} className="p-8 text-center text-secondary">No messages yet.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
